import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';

const Model = require('UNIT_PATH/model');
const View = require('UNIT_PATH/view');

class Notify{
	static getNotifyStatus() {
		Model.getActNotifyStatus(function(data) {
			View.renderActNotifyStatus(data);
		});
	}

	static notifyEventBind() {
		$('.schedule-btn').click(function() {
			var _this = this;

			if($(this).attr('update') == 'false') {
				return;
			}
			
			Model.notifySwitchRequest($(_this), function(data) {
				View.renderNotifyResult($(_this), data);
			});
		});
	}
}

module.exports = Notify;


