import $ from 'ZEPTO_PATH';
var UTIL = require('UTILS_PATH/helper');

class Timer {
	constructor(opts) {
		this.opts = opts;
		this.timerBox = opts.timerBox;
		this.disTime = parseInt(opts.timerBox.attr('data-time'), 10);
		this.timer = null;
	}

	_run() {
		if(this.timer) {
			clearInterval(this.timer);
			this.timer = null;
		}
		
		this._runFuc();
		this.timer = setInterval(this._runFuc.bind(this), 1000);
	}

	_runFuc() {
		var time_data = UTIL.timeTransform(this.disTime / 1000);

		// 时
		$('.hour', this.timerBox).eq(0).text(time_data.hour);

		// 分
		$('.minute', this.timerBox).eq(0).text(time_data.min);

		// 秒
		$('.second', this.timerBox).eq(0).text(time_data.sec);

		this.disTime -= 1000;
		if(this.disTime < 0) {
			clearInterval(this.timer);
			this.timer = null;
			this.opts.timeUp && this.opts.timeUp();
		}
	}
}

module.exports = Timer;


