import $ from 'ZEPTO_PATH';
import Swiper from 'SWIPER_JS_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import updateSlider from 'UNIT_PATH/updateSlider';

function init() {
	if(!VARIABLE.CONFIG.SCROLL_OBJ) {
		var red_nav = $('#red-nav');
		var scroller_view = $('#scroll-view');
		var jd_nav = $('#jd-nav');
		var body_dom = $('body');
		var top, header_height;

		$(window).scroll(function(){
			top = body_dom.scrollTop();
			header_height = jd_nav.height();

			if(!$('.red-nav-box').length) return;

			if(top >= header_height) {
				red_nav.addClass('fix-top');
				scroller_view.addClass('pt60');
			} else if(top < header_height) {
				red_nav.removeClass('fix-top');
				scroller_view.removeClass('pt60');
			}
		});
	}

	var this_swiper  = $('.red-nav-box');
	var navSwiper = new Swiper('.red-mobile-nav',{
		slidesPerView : 'auto',
		loop: false,
		preventClicks : true,
		freeMode: true,
		freeModeSticky: true,
		onClick: function(swiper) {

		},
		onInit: function(swiper) {
			var active_slider = $('.swiper-slide.active', this_swiper).get(0);

			if(active_slider) {
				slide_to(swiper, active_slider, this_swiper, 0);
			};
		}
	});

	// 浏览器端旋转设备时刷新尺寸
	if(VARIABLE.CONFIG.VS != 'jdapp') {
		$(window).bind('orientationchange', function() {
			var active_slider = $('.swiper-slide.active', this_swiper).get(0);

			if(active_slider) {
				updateSlider(navSwiper);
				slide_to(navSwiper, active_slider, this_swiper, 0);
			}			
		});
	}
}

function slide_to(swiper, active_slider, swiper_box, time) {
	var li_fix            = $('.nav-fix-item');
	var swiper_width_half = parseInt(swiper_box.width() / 2, 10);
	var offset_left       = active_slider.offsetLeft + li_fix.width();
	var max_translate     = swiper.maxTranslate();
	var min_translate     = swiper.minTranslate();
	var translate_to      = swiper_width_half - offset_left - active_slider.offsetWidth / 2;

	if(translate_to > min_translate) {
		translate_to = min_translate;
	}else if(translate_to < max_translate) {
		translate_to = max_translate;
	}
	swiper.setWrapperTransition(time);
	swiper.setWrapperTranslate(translate_to);
}

function navLayout(swiper) {
	var virtualWidth = swiper.virtualSize;
	var winWidth = $('.red-page').width();
	var allslides = swiper.slides;

	if(allslides.length < 5) {
		$('.red-mobile-nav li').width(winWidth/allslides.length);
	}
}

module.exports = {
	init: init
}


